.repeatable-field {
	padding: 0;
	border: none;

	&__rows,
	&__bottom {
		max-width: 400px;
	}

	&__rows {
		margin: 0;
		padding: 0;
		list-style-type: none;
	}

	&__rows > &__row + &__row {
		margin-top: 0.815rem;
	}

	&__row:only-child &__remove-button {
		display: none;
	}

	&__row-wrap {
		display: flex;
		align-items: center;
	}

	&__input {
		flex: 1;
		margin-right: 5px !important;
		width: 50% !important;
	}

	&__input:last-child {
		margin-right: 0 !important;
	}

	&__input--radio {
		min-width: 16px !important;
		max-width: 16px !important;
		width: 16px !important;
		height: 16px !important;
		margin-top: 0 !important;
	}

	&__remove-button {
		margin-left: 0.5rem;
		padding: 0.25rem;
		color: #b91c1c;
		background: none;
	}

	&__remove-button:hover,
	&__remove-button:focus {
		text-decoration: underline;
		box-shadow: none;
	}

	&__bottom {
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin-top: 1rem;
	}

	&__limit-counter {
		font-size: 14px;
		color: #555;
	}

	&__add-button {
		display: block;
		font-size: 14px;
	}
}
